import request from "@/utils/request";

export function fetchCustomerNameList() {
  // console.log(params)
  let customerList = {
    code: 200,
    message: "success",
    data: {
      list: [{
        customerId: 1,
        customerName: "Tom"
      }, {
        customerId: 2,
        customerName: "Jone"
      }]
    }
  };
  return new Promise(resolve => {
    resolve(customerList)
  })
  return request({
    url: '/customer/nameList',
    method: 'get'
  })
}

export function fetchCustomerList(params) {
  // console.log(params)
  let customerList = {
    code: 200,
    message: "success",
    data: {
      list: [{
        customerId: 1,
        customerName: "Tom",
        customerTel: "13812344321",
        createTime: 1627747200000,
        other: "xxx"
      }, {
        customerId: 2,
        customerName: "Jone",
        customerTel: "Jone@163.com",
        createTime: 1627747200000,
        other: "xxx"
      }],
      total: 11
    }
  };
  return new Promise(resolve => {
    resolve(customerList)
  })
  return request({
    url: '/customer/list',
    method: 'get',
    params: params
  })
}

export function updateCustomer(id, data) {
  let res = {
    code: 200,
    message: "success"
  };
  return new Promise(resolve => {
    resolve(res)
  })

  return request({
    url: '/customer/update/' + id,
    method: 'post',
    data: data
  })
}

export function delCustomer(id) {
  let res = {
    code: 200,
    message: "success"
  };
  return new Promise(resolve => {
    resolve(res)
  })

  return request({
    url: '/customer/del/' + id,
    method: 'post'
  })
}

export function addCustomer(data) {
  let res = {
    code: 200,
    message: "success"
  };
  return new Promise(resolve => {
    resolve(res)
  })

  return request({
    url: '/customer/add',
    method: 'post',
    data: data
  })
}



