import request from "@/utils/request";

export function fetchStockList(params) {
  // console.log(params)
  let stockList = {
    code: 200,
    message: "success",
    data: {
      list: [{
        stockId: 1,
        firm: "Michelin",
        stock: "轮胎",
        model: "215/70R15",
        unit: "个",
        num: 10,
        price: 639,
        other: "新品耐越上市，换新升级"
      }, {
        stockId: 2,
        firm: "Michelin",
        stock: "轮胎2",
        model: "215/70R15-2",
        unit: "个",
        num: 11,
        price: 639,
        other: "新品耐越上市，换新升级"
      },{
        stockId: 3,
        firm: "喜马诺",
        stock: "飞轮",
        model: "HG200-9",
        unit: "个",
        num: 12,
        price: 62,
        other: "齿数：11-13-15-17-19-21-24-28-32"
      },{
        stockId: 4,
        firm: "喜马诺",
        stock: "飞轮2",
        model: "HG200-9-2",
        unit: "个",
        num: 13,
        price: 62,
        other: "齿数：11-13-15-17-19-21-24-28-32"
      }],
      total: 11
    }
  };
  return new Promise(resolve => {
    resolve(stockList)
  })
  return request({
    url: '/stock/list',
    method: 'get',
    params: params
  })
}

export function fetchFirmList() {
  let firmList = {
    code: 200,
    message: "success",
    data: {
      list: [{
        firm: "Michelin"
      }, {
        firm: "Dunlop"
      }]
    }
  };
  return new Promise(resolve => {
    resolve(firmList)
  })
  return request({
    url: '/stock/firmList',
    method: 'get'
  })
}

export function updateStock(id, data) {
  let res = {
    code: 200,
    message: "success"
  };
  return new Promise(resolve => {
    resolve(res)
  })

  return request({
    url: '/stock/update/' + id,
    method: 'post',
    data: data
  })
}

export function delStock(id) {
  let res = {
    code: 200,
    message: "success"
  };
  return new Promise(resolve => {
    resolve(res)
  })

  return request({
    url: '/stock/del/' + id,
    method: 'delete'
  })
}

export function updateStockList(data) {
  let res = {
    code: 200,
    message: "success"
  };
  return new Promise(resolve => {
    resolve(res)
  })

  return request({
    url: '/stock/list',
    method: 'post',
    data: {list: data}
  })
}



